		//获取用户信息
		getUsers(){
			var THAT = this;
			THAT.userList=[];
			/**********************************************/
			//同步获取限制次数
			try {
			    THAT.slideNumber = uni.getStorageSync('slideNumber');
			    if (THAT.slideNumber) {
